
.loginmain{
    
        display:flex;
        flex-direction:column;
        justify-content:center;
        align-items:center;
        min-height:100vh;
        background:url("background.jpg") no-repeat;
        background-size:cover;
        background-position:center;
      
      
      
}
.Header{
        position:absolute;
        top:0;
        left :0;
        width:100%;
        padding:20px 100px;
        /* background:aqua; */
        display:flex;
        justify-content: space-between;
        align-items:center;
        z-index:99;
    }
  
    
    .Header__button{
        left:0px;
        top:10px;
        position:absolute;
        width:90px;
        height:40px;
        background:transparent;
        border:2px solid aliceblue;
        outline:none;
        border-radius:6px;
        cursor:pointer;
        font-size:1.2em;
        color:aliceblue;
        font-weight:500;
        margin-left:40px;
        transition:.5s;
    }
    /* use aliceblue or #fff */
    .Header__button:hover{
        background:aliceblue;
        color:#162938;
    }